<template>
  <div>
    <div class="contentCellBox">
      <div class="contentCellBody flexStart">
        <div class="taskAuditDataExtraction-left" style="width:100%">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>流程信息
          </div>
          <Row>
            <table class="tableBox" style="margin-top:0">
              <thead>
                <tr>
                  <td>流程名称</td>
                  <td colspan="8">{{addNm}}{{processTable.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>流程步骤</td>
                  <td>发起时间</td>
                  <td>结束时间</td>
                  <td>办理人</td>
                  <td>办理人部门</td>
                  <td>结果</td>
                  <td>意见</td>
                  <td>下一步骤</td>
                  <td>下一处理人</td>
                </tr>
                <tr v-for="(item ,index) in processTable.content">
                  <td>{{item.actionName}}</td>
                  <td>{{item.startTime}}</td>
                  <td>{{item.endTime}}</td>
                  <td>{{item.userName}}</td>
                  <td>{{item.userDeptName}}</td>
                  <td>{{item.approveResult==0||item.approveResult=='null'?'审核通过':'审核退回'}}</td>
                  <td
                    class="opinions"
                  >{{item.approveNote=='null'?'通过':item.approveNote}}</td>
                  <td>{{item.nextActivityName}}</td>
                  <td>{{item.nextUserName}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['dataObjj'],
  data() {
    return {
      addNm:'',
      processTable: {
        name: "产品洗钱风险评估审核流程",
        content: [
          // {
          //   step: "确认",
          //   startTime: "2019-02-13 12:12:32",
          //   endTime: "2019-02-13 15:12:32",
          //   pepole: "李明远",
          //   part: "零售运维管理部",
          //   result: "--",
          //   text: "XXXXXX",
          //   nextStep: "部门反洗钱专员审核",
          //   nextPeople: "李打"
          // },
          // {
          //   step: "审核",
          //   startTime: "2019-02-13 12:12:32",
          //   endTime: "2019-02-13 15:12:32",
          //   pepole: "李明远",
          //   part: "零售运维管理部",
          //   result: "同意",
          //   text: "XXXXXX",
          //   nextStep: "部门反洗钱专员审核",
          //   nextPeople: "刘表"
          // },
          // {
          //   step: "审核",
          //   startTime: "2019-02-13 12:12:32",
          //   endTime: "2019-02-13 15:12:32",
          //   pepole: "李明远",
          //   part: "零售运维管理部",
          //   result: "同意",
          //   text: "XXXXXX",
          //   nextStep: "--",
          //   nextPeople: "--"
          // }
        ]
      }
    };
  },
  created(){
      let documentId='';
      if(this.dataObjj.type=='评估'){
          documentId=this.dataObjj.evalBusinessId
      }else if(this.dataObjj.type=='调整'){
          documentId=this.dataObjj.adjBusinessId
      }
    // ------------
    this.axios
      .post(`/bpm/getBpm/${documentId}`)
      .then(response => {
        if (response.code == '200') {
          this.processTable.content = response.data.dataVo.records.comments;
        }else{
          this.$Message.error(response.message)
        }
      })
      .catch(error => {});
  },
  methods: {
    
  }
};
</script>
<style scoped>
.fileBox {
  padding: 20px 0;
}
.fileBox > p {
  width: 200px;
  text-align: left;
  height: 20px;
  line-height: 16px;
  border: 1px solid RGB(241, 243, 244);
  margin: 10px;
  padding: 2px;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
}
.fileBox > p:hover {
  background-color: RGB(45, 140, 240);
  color: white;
}
</style>


